// 1. OBJECT [ 1.0. GLOBAL ] ##########################################################################################

// A. GLOBAL STYLES +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// A.1. GENERAL SETTINGS ----------------------------------

html, body { font-family: $default; font-weight: 100; overflow-x: hidden; -webkit-overflow-scrolling: touch; }

body {
  //&.modal-open { @include position(absolute, 0); overflow: hidden; }
}


.p-main { height: 100%; position: relative; }

// A.1. END -----------------------------------------------

// A.2. GENERAL -------------------------------------------

.alignleft { float: left; margin-right: $base; margin-bottom: $base; width: 100%; height: auto; }

// A.2. END -----------------------------------------------

// A.3. ELEMENTS ------------------------------------------

.m-logo { @include aspect-ratio( 60%, 50% ); margin: 0 auto;
  svg { @include position (absolute, 0); width: 100%; height: 100%; }
}

.m-video-block { @include aspect-ratio(100%,56%); position: relative;
  iframe { @include position (absolute, 0); width: 100%!important; height: 100%!important; }
}

.nocomments { display: none; }

.toggle-tab { background: $grey-f2;
  &.on { background: white; }
}

.a-text-link { text-align: center; transition: 0.3s; text-decoration: none!important; color: black;
  span { display: block; padding: 8px 0; position: relative; font-family: $limo; font-size: 18px;
    &:after { position: absolute; content: ''; background-color: $grey-80; height: 2px; width: 100%; bottom: 0; @include center-align();  transition: 0.3s;}
  }
  &:hover { color: $grey-80;
    span {
      &:after { width: 0; font-weight: 600; transition: 0.3s; }
    }
  }
}

// A.3. END -----------------------------------------------

// A. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

