// 3. ORGANISM [ 3.1. CARDS ] #########################################################################################

// A. GLOBAL STYLES +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// A.1. GENERAL CARDS -------------------------------------------------------------------------------------------------

.o-block {
  &.carousel-block { position: fixed; width: 100%; height: 100%;

    .swiper-container { height: 100%;
      .swiper-wrapper { height: 100%;
        .swiper-slide { height: 100%;
          .carousel-pic {
            @include position (absolute, 0);
            width: 100%;
            height: 100%;
            background-size: cover;
            background-position: center;
            background-color: black;
            z-index: 10;
          }
          .carousel-text { position: absolute; z-index: 100;
            .text { color: #FFFFFF; text-shadow: 0 0 10px #000000; line-height: normal; margin: 0; }
            h3 { font-size: 5vh; }
            h5 { font-size: 3vh; }
          }
        }
      }
    }

    .swiper-button {
      @include vertical-align(absolute);
      width: 15px;
      height: 25px;
      background-size: cover;
      display: none;
      opacity: 0.3;
      transition: 0.2s;
      outline: none!important;

      &.swiper-button-next {
        background-image: url('../../dist/images/site/button--arrow-right.svg'); right: 6px; left: auto;
      }

      &.swiper-button-prev {
        background-image: url('../../dist/images/site/button--arrow-left.svg'); left: 6px; right: auto;
      }

      &:hover { opacity: 1; transition: 0.2s; width: 17px; height: 27px; }

    }

    .swiper-pagination {
      .swiper-pagination-bullet { width: 16px; height: 4px; border-radius: 0;
        &.swiper-pagination-bullet-active {  }
      }
    }


  }

  &.block-team {
    .swiper-pagination { position: relative; bottom: 0; }
  }
}

.o-staff-carousel {
  .slick-arrow {
    @include vertical-align(absolute);
    width: 15px;
    height: 15px;
    border: 0;
    border-radius: 30px;
    font-size: 0;
    background: $grey-f2;
    cursor: pointer;
    &:before { position: absolute; content: ''; width: 15px; height: 15px; background: $grey-f2; border-radius: 30px; bottom: -25px; left: 0; }
    &:after { position: absolute; content: ''; width: 15px; height: 15px; background: $grey-f2; border-radius: 30px; top: -25px; left: 0; }
    &.slick-next { right: -30px; }
    &.slick-prev { left: -30px; }
  }
}

// A.1. END -----------------------------------------------------------------------------------------------------------

// A. GLOBAL STYLES +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// B. MAX-WIDTH: 767px ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

@media ($sm-dn) {

  .o-staff-carousel {
    .slick-arrow { display: none!important; }
  }

}

// B. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// C. MIN-WIDTH: 768px ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

@media ($sm-up) {} 

// C. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// D. MIN-WIDTH: 992px ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

@media ($md-up) {

  .o-staff-carousel {
    .slick-arrow {
      width: 30px;
      height: 30px;
      background-color: $grey-f2;
      background-position: center;
      background-size: 20px 20px;
      background-repeat: no-repeat;
      outline: none!important;
      &:before { display: none; }
      &:after { display: none; }
      &.slick-next { right: -50px; background-image: url('../images/site/button--arrow-right.svg'); }
      &.slick-prev { left: -50px; background-image: url('../images/site/button--arrow-left.svg'); }
      &:hover { background-color: $grey-b3; }
    }
  }

}

// D. END +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

// E. MIN-WIDTH: 1200px +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++

